<template>
  <!-- 性别 -->
  <div class="book-sex">
    <!-- 标题 -->
    <div class="sex-title">
      <p class="animated bounceInLeft">欢迎来到哎读小说</p>
      <p class="animated bounceInLeft">选择性别,可为您推荐不同的内容哦 ~</p>
    </div>
    <!-- 性别选择 -->
    <div class="sex-select">
      <div class="select">
        <!-- 男 -->
        <div class="sex-boy flt">
          <div class="flt-img">
            <div class="seximg animated bounceInDown">
              <img class="max-img" src="../../image/boy.jpeg" />
            </div>
          </div>
          <div class="badgit">
            <div class="logo-hot animated bounceInLeft" :class="{logoselect :isAls1}" @click="select1(isAls1)">
              <p>男生</p>
            </div>
          </div>
        </div>
        <!-- 女 -->
        <div class="sex-girst flt">
          <div class="flt-img">
            <div class="seximg animated bounceInDown">
              <img class="max-img" src="../../image/girl.jpeg" />
            </div>
          </div>
          <div class="badgit">
            <div class="logo-hot animated bounceInLeft" :class="{logoselect :isAls2}" @click="select2(isAls2)">
              <p>女生</p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- 下一步指令 -->
    <div class="sex-btn">
      <div class="btn animated fadeInLeft">
        <van-button
          color="linear-gradient(to right, #4bb0ff, #6149f6)"
          round
          block
          :loading="isLoading"
          type="primary"
          loading-type="spinner"
          loading-text="设置中..."
          @click="sexbtn()"
        >下一步</van-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //默认选择为男生
      isAls1: true,
      isAls2: false,
      isLoading: false,
    };
  },
  computed: {},
  methods: {
    select1(isAls1) {
      this.isAls1 = true;
      if (this.isAls1) {
        this.isAls2 = false;
        return;
      }
    },
    select2(isAls2) {
      this.isAls2 = true;
      if (this.isAls2) {
        this.isAls1 = false;
        return;
      }
    },
    sexbtn() {
      let randomTime = Math.random() * 3 * 1000;
      this.isLoading = true ;
     setTimeout(() =>{
        //获取所有用户
      let users = localStorage.getItem("users");
      users = users ? JSON.parse(users) : [];
      //获取当前用户信息
      let user = localStorage.getItem("user");
      user = user ? JSON.parse(user) : [];
      for (let i = 0; i < users.length; i++) {
        //匹配用户
        if (users[i].username == user.username) {
          //匹配到用户
          if (this.isAls1) {
            users[i].sex = true;
            user.sex = true;
          } else {
            users[i].sex = false;
            user.sex = false;
          }
          localStorage.setItem("users", JSON.stringify(users));
          localStorage.setItem("user", JSON.stringify(user));
          this.$toast({
                message: "设置成功"
              });
          return this.$router.push({ name: "forum" });
        }

      }
     },randomTime)
    }
  },
  components: {},
  created() {}
};
</script>
<style lang="less">
p {
  margin: 0;
}
.max-img {
  display: block;
  width: 100%;
  height: 100%;
}
.book-sex {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: white;
  .sex-title {
    width: 100%;
    margin-top: 50px;
    p {
      text-align: center;
    }
    p:nth-child(1) {
      font-size: 22px;
      font-weight: 600;
    }
    p:nth-child(2) {
      font-size: 12px;
      margin-top: 5px;
      color: #999;
    }
  }
  .sex-select {
    width: 100%;
    overflow: hidden;
    margin-top: 100px;
    .select {
      width: calc(100% - 100px);
      overflow: hidden;
      padding: 0 50px;
      .flt {
        float: left;
        width: 50%;
        overflow: hidden;
        .flt-img {
          width: 100%;
          height: 100px;
          float: left;
        }
        .seximg {
          width: 100px;
          height: 100px;
          margin: 0 auto;
          border-radius: 50%;
          background: red;
          overflow: hidden;
          img {
          }
        }
        .badgit {
          float: left;
          width: 100%;
          height: 40px;
          margin-top: 15px;
          text-align: center;
          .logoselect {
            border: 1px solid #4bb0ff;
            box-shadow: 0px 0px 8px #4bb0ff;
          }
          .logo-hot {
            width: calc(100% - 50px);
            margin: 9px 23px;
            padding: 5px 0;
            border-radius: 15px;
          }
          p {
            font-size: 14px;
            font-weight: 600;
          }
        }
      }
    }
  }
  .sex-btn {
    width: 100%;
    overflow: hidden;
    position: absolute;
    left: 0;
    bottom: 50px;
    .btn {
      width: calc(100% - 50px);
      padding: 0 25px;
    }
  }
}
</style>